import "./App.css";
import React, { useState } from "react";

import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import TouchAppIcon from '@material-ui/icons/TouchApp';

const ITEM_HEIGHT = 48;
const buttonCaterories = [
  "animal",
  "career",
  "celebrity",
  "dev",
  "explicit",
  "fashion",
  "food",
  "history",
  "money",
  "movie",
  "music",
  "political",
  "religion",
  "science",
  "sport",
  "travel",
];

function App() {
  const [jokes, setJokes] = useState(null);
  const [divname,setDivname] = useState(true);

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const GetChuckjokes = async (event) => {
    await fetch(`https://api.chucknorris.io/jokes/random?category=${event}`)
      .then((res) => res.json())
      .then((joke) => {
        if (joke.status === 404) {
          alert("No joke found for this category");
        } else {
          setJokes(joke);
        }
      })
      .catch((err) => {
        console.log("ERROR occured");
      });
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className="App">
      <div>
        <h1 className="name">
          Click button Below to select and view chuck joke
        </h1>
      </div>
      <div className="container">
        <IconButton
          className="button"
          style={{ color: "red" }}
          aria-label="more"
          aria-controls="long-menu"
          aria-haspopup="true"
          onClick={(e)=>{handleClick(e);
          setDivname(!divname)}}
        >
          Click me to select
       { divname &&  <div className="icon_shake">
          <TouchAppIcon  style={{ fontSize: 40,color:"yellow" }}/>
          </div>}
        </IconButton>
        <Menu
          id="long-menu"
          anchorEl={anchorEl}
          keepMounted
          open={open}
          onClose={(e) => {
            handleClose();
          }}
          PaperProps={{
            style: {
              maxHeight: ITEM_HEIGHT * 4.5,
              width: "20ch",
            },
          }}
        >
          {buttonCaterories.map((option) => (
            <MenuItem
              key={option}
              onClick={(e) => {
                console.log(e.target.innerText);

                GetChuckjokes(e.target.innerText);
                setDivname(!divname);
                handleClose();
              }}
            >
              {option}
            </MenuItem>
          ))}
        </Menu>
      </div>
      <div>
        {jokes && (
          <div class="box">
            <div>
              <img className="avatar" src={jokes.icon_url} alt="new" />
            </div>
            <div class="glass"></div>
            <div class="content">
              <h2>{jokes.categories} Joke</h2>
              <p>{jokes.value}</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
